      this.debugLogger.warn('ìƒë‹´ì›ëŒ€ì‹œë³´ë“œ', 'âš ï¸ ì¼ë¶€ ê¸°ëŠ¥ì´ ì œí•œë  ìˆ˜ ìˆì§€ë§Œ ëŒ€ì‹œë³´ë“œëŠ” ê³„ì† ì‚¬ìš© ê°€ëŠ¥í•©ë‹ˆë‹¤');
      
