  /**
   * WebSocket ì—°ê²° ë° ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì„¤ì •
   */
  private async connectWebSocket() {
    this.debugLogger.info('WebSocket', 'ğŸ”Œ WebSocket ì—°ê²° ì‹œì‘');
    
    try {
      // WebSocket ì—°ê²° ëŒ€ê¸°
      await this.socketService.connect();
      this.debugLogger.success('WebSocket', 'âœ… WebSocket ì—°ê²° ì™„ë£Œ');
      
      // ì—°ê²° ì™„ë£Œ í›„ ìƒë‹´ì› ë“±ë¡
      this.debugLogger.info('WebSocket', `ğŸ“ ìƒë‹´ì› ë“±ë¡ - agentId: ${this.agentId}, orgId: ${this.organizationId}`);
      this.socketService.registerAgent(this.agentId, this.organizationId);
      
      // ğŸ” ëª¨ë“  ì´ë²¤íŠ¸ ë¡œê¹… (ë””ë²„ê¹…ìš©)
      this.socketService.onAnyEvent((eventName: string, data: any) => {
        this.debugLogger.info('ì´ë²¤íŠ¸ìˆ˜ì‹ ', `ğŸ¯ ${eventName}`);
      });
      
    } catch (error) {
      this.debugLogger.error('WebSocket', `âŒ WebSocket ì—°ê²° ì‹¤íŒ¨: ${error}`);
    }

    // âœ… ìƒˆ ìƒë‹´ ìš”ì²­ ì•Œë¦¼ (ë°±ì—”ë“œ ì´ë²¤íŠ¸: newSupportRequest)
    const newRequestSub = this.socketService.onEvent('newSupportRequest').subscribe((data: any) => {
      this.debugLogger.info('ìƒë‹´ì›ëŒ€ì‹œë³´ë“œ', `ğŸ”” ìƒˆ ìƒë‹´ ìš”ì²­ ìˆ˜ì‹ !`);
      this.debugLogger.info('ìƒë‹´ì›ëŒ€ì‹œë³´ë“œ', `ğŸ“Š roomId: ${data?.room?.id}`);
      this.debugLogger.info('ìƒë‹´ì›ëŒ€ì‹œë³´ë“œ', `ğŸ‘¤ clientId: ${data?.room?.clientId}`);
      this.debugLogger.info('ìƒë‹´ì›ëŒ€ì‹œë³´ë“œ', `ğŸ“Œ status: ${data?.room?.status}`);
      this.handleNewSupportRequest(data.room);
    });

    // âœ… ìƒˆ ë©”ì‹œì§€ ìˆ˜ì‹  (ë°±ì—”ë“œ ì´ë²¤íŠ¸: createChat)
    const newMessageSub = this.socketService.onEvent<{ chat: any }>('createChat').subscribe((data: any) => {
      if (data?.chat) {
        this.debugLogger.info('ë©”ì‹œì§€ìˆ˜ì‹ ', `ğŸ’¬ ìƒˆ ë©”ì‹œì§€ (createChat) - ${data.chat.senderType}: ${data.chat.content?.substring(0, 20)}...`);
        this.handleNewMessage(data.chat);
      }
    });

    // âœ… ìƒë‹´ ìˆ˜ë½ ì„±ê³µ (ë°±ì—”ë“œ ì´ë²¤íŠ¸: assignAgentSuccess)
    const assignSuccessSub = this.socketService.onEvent('assignAgentSuccess').subscribe(async (data: any) => {
      this.debugLogger.success('WebSocket', `âœ… ìƒë‹´ ìˆ˜ë½ ì„±ê³µ - roomId: ${data?.roomId}`);
      
      // ì±„íŒ…ë°© ëª©ë¡ ìƒˆë¡œê³ ì¹¨
      await this.loadRooms();
      
      // ìˆ˜ë½í•œ ì±„íŒ…ë°© ìë™ ì„ íƒ
      if (data?.roomId) {
        const room = this.allRooms.find(r => r.id === data.roomId);
        if (room) {
          await this.selectRoom(room);
        }
      }
    });

    // ê³ ê° íƒ€ì´í•‘ ìƒíƒœ (ì¶”í›„ êµ¬í˜„)
    const clientTypingSub = this.socketService.onEvent('clientTyping').subscribe((data: any) => {
      this.debugLogger.info('WebSocket', `âœï¸ ê³ ê° íƒ€ì´í•‘ ì¤‘ - roomId: ${data?.roomId}`);
      this.handleClientTyping(data);
    });

    // ì±„íŒ…ë°© ì¢…ë£Œ (ì¶”í›„ êµ¬í˜„)
    const roomClosedSub = this.socketService.onEvent('roomClosed').subscribe((data: any) => {
      this.debugLogger.info('WebSocket', `ğŸšª ì±„íŒ…ë°© ì¢…ë£Œ - roomId: ${data?.roomId}`);
      this.handleRoomClosed(data.roomId);
    });

    this.subscriptions.push(newRequestSub, newMessageSub, assignSuccessSub, clientTypingSub, roomClosedSub);
    this.debugLogger.info('WebSocket', 'ğŸ“¡ ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ë“±ë¡ ì™„ë£Œ');
    this.debugLogger.info('WebSocket', 'ğŸ¯ ì´ë²¤íŠ¸ ìˆ˜ì‹  ëŒ€ê¸° ì¤‘...');
  }

